.container-center{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #121212;
}

.login{
    background: #eaeaec;
    width: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.login-area{
    display: flex;
    justify-content: center;
    background-color: #000303;
    width: 100%;
}

.login-area img{
    padding: 20px;
    width: 170px;
    height: 130px;
}

form{
    margin-top: 1.5em;
    width: 90%;
    display: flex;
    flex-direction: column;
}

form h1{
    text-align: center;
    margin-bottom: 0.5rem;
    color: #000303;
}

.login input {
    margin-bottom: 15px;
    height: 35px;
    border: 0;
    border-radius: 7px;
    padding: 10px;
    font-size: 15px;
    background-color: #fff;
}

form button{
    height: 35px;
    border: 0;
    border-radius: 7px;
    background-color: #000303;
    color: white;
    font-size: 1.3rem;
}

.login a{
    margin: 1.5em 0;
    color: #000;
    cursor: pointer;
}